import { useEffect, useState, useRef } from "react";
import { Container, Row, Col, Card, Form, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import { GrDocumentCloud } from "react-icons/gr";
import { AiOutlineClose } from "react-icons/ai";
import { FaRegFilePdf, FaRegSmile } from "react-icons/fa";
import { BsImage } from "react-icons/bs";
import { FiPaperclip } from "react-icons/fi";
import Picker from "emoji-picker-react";
import IMAGES from "../../../assets/images";
import {
  getAllMessage,
  getInboxList,
  messageIsRead,
  pushSendMessade,
  sendMessage,
} from "../../../app/features/chat/chat.slice";
import convertBase64, {
  convertTimeIntoUTC,
  Notification,
} from "../../../components/GenericActions";
import { socket } from "../../../authorization/ProtectedRoute";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import AWSImageUpload from "../../../app/features/bucketImageUpload/bucketImageUpload.service";
import SearchInput from "../../../components/SearchInput";

const Chat = () => {
  const bottomRef = useRef(null);
  const inputRef = useRef(null);
  const [newMessage, setNewMessage] = useState("");
  const [selectedChat, setSelectedChat] = useState();
  const [showPicker, setShowPicker] = useState(false);
  const [selectedFiles, setSelectedFiles] = useState(null);
  const [uploadImageURL, setUploadImageURL] = useState(null);

  const [searchValue, handleSearch] = useDebouncedSearch(500);
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);
  const {
    allInboxList,
    allMessages,
    isLoadingAllInboxList,
    isSuccessAllInboxList,
    isErrorAllInboxList,
  } = useSelector((state) => state?.chat);

  const userId = user?.userId;

  const handleChatClick = (chat) => {
    setSelectedChat(chat);
    setNewMessage("");
    setSelectedFiles(null);
    const finalData = {
      inboxId: chat?.inboxId || selectedChat?.inboxId,
    };
    dispatch(messageIsRead(finalData));
  };

  const onEmojiClick = (emojiObject) => {
    setNewMessage((prevInput) => prevInput + emojiObject?.emoji);
    setShowPicker(false);
  };
  useEffect(() => {
    if (socket) {
      socket?.on("connect", () => {
        console.log("signal1", socket?.connected);
      });
      socket?.emit("connectToUserRoom", { userId: userId }, () => {
        console.log("signal2", socket?.connected);
        // setAgainCheckStatus(true);
      });
      if (!socket?.connected) {
        socket?.on("reconnect", () => {
          console.log("reconnect", socket?.connected);
        });
      }
      if (socket?.connected) {
        console.log("refttytfconnect", socket?.connected);
      }
      socket.on("disconnect", () => {
        console.log("Disconnected");
      });
    }
    return () => {
      socket.off("connect");
      socket.off("connected");
      socket.off("disconnect");
      socket.off("messageData");
    };
  }, [userId, socket]);

  useEffect(() => {
    const handleMessageData = (data) => {
      if (data?.data) {
        const jsonData = data?.data?.data;
        const senderMessage = JSON.parse(jsonData);
        const finalData = {
          createdDate: convertTimeIntoUTC(new Date()),
          file: senderMessage?.file ? senderMessage?.file : null,
          fileUrl: senderMessage?.fileUrl ? senderMessage?.fileUrl : null,
          fromUserId: senderMessage?.fromUserId,
          fromUserName: senderMessage?.fromUserName
            ? senderMessage?.fromUserName
            : selectedChat?.userName
            ? selectedChat?.userName
            : null,
          image: senderMessage?.image ? senderMessage?.image : null,
          imageUrl: senderMessage?.imageUrl ? senderMessage?.imageUrl : null,
          inboxId: senderMessage?.inboxId,
          message: senderMessage?.message,
          messageId: senderMessage?.messageId ? senderMessage?.messageId : null,
          toUserId: senderMessage?.toUserId,
          toUserName: senderMessage?.toUserName
            ? senderMessage?.toUserName
            : null,
        };
        dispatch(pushSendMessade(finalData));
      }
    };
    if (socket?.connected) {
      socket.on("messageData", handleMessageData);
    }

    return () => {
      socket.off("messageData", handleMessageData);
    };
  }, [socket, selectedChat]);

  const moveToNext = (data) => {
    socket.emit("sendMessage", {
      userIds: [
        {
          id: selectedChat?.userId,
        },
      ],
      data: JSON.stringify(data?.data),
      // uuid: v4(),
    });
  };

  const handleChangeUpload = async (e) => {
    let file = e.target.files[0];
    setSelectedFiles(file);
    if (file) {
      const lastIndexOfDot = file.name.lastIndexOf(".");
      const name = file.name.slice(0, lastIndexOfDot);
      const ext = file.name.slice(lastIndexOfDot + 1, file.name.length);
      // convertBase64(file, (url) => {
      if (ext.toLowerCase() === "pdf") {
        const reader = new FileReader();
        reader.onload = async (event) => {
          const base64String = event.target.result;

          const fileData = {
            name: name,
            base64: base64String.split(",")[1],
            fileExtension: `${ext}`,
          };
          AWSImageUpload(fileData).then((response) => {
            if (response?.keyName) {
              setUploadImageURL(response);
              Notification("File uploaded.", true);
            } else {
              Notification("File too large. Max size is 500KB.", false);
            }
          });
        };
        reader.onerror = (error) => {
          console.log("error", error);
          // Handle the error
        };

        reader.readAsDataURL(file);
      } else {
        // Handle non-PDF file types here
        // For other file types, you can use your existing code to convert to Base64
        convertBase64(file).then((url) => {
          const fileData = {
            name: name,
            base64: url,
            fileExtension: `${ext}`,
          };

          AWSImageUpload(fileData).then((response) => {
            if (response?.keyName) {
              setUploadImageURL(response);
              Notification("File uploaded.", true);
            } else {
              Notification("File too large. Max size is 750KB.", false);
            }
          });
        });
      }
    }
  };
  const handleRemoveFile = () => {
    setSelectedFiles(null);
    setUploadImageURL(null);
    document.getElementById("file-upload").value = null;
  };

  const handleNewMessageSubmit = (event) => {
    event.preventDefault();

    const finalData = {
      inboxId: selectedChat?.inboxId,
      fromUserId: user?.userId,
      toUserId: selectedChat?.userId,
      message: newMessage.trim() || "",
      file: uploadImageURL?.keyName || "",
      fileUrl: uploadImageURL?.baseUrl || "",
      isChat: true,
    };
    dispatch(sendMessage({ finalData, moveToNext }));
    setNewMessage("");
    setSelectedFiles(null);
    setUploadImageURL(null);
  };

  useEffect(() => {
    const finalData = {
      inboxId: selectedChat?.inboxId,
    };
    dispatch(getAllMessage(finalData));
  }, [dispatch, selectedChat?.inboxId]);

  useEffect(() => {
    const finalData = {
      search: searchValue || null,
    };
    dispatch(getInboxList(finalData));
  }, [dispatch, searchValue]);

  useEffect(() => {
    // ðŸ‘‡ï¸ scroll to bottom every time messages change
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [allMessages]);

  const handleKeyDown = (e) => {
    // if (e.which === 32 && inputRef.current.selectionStart === 0) {
    //   e.preventDefault();
    // }
    if (e.key === "Enter") {
      e.preventDefault();
      if (newMessage.trim() !== "" || uploadImageURL?.keyName) {
        handleNewMessageSubmit(e);
      }
    }
  };

  useEffect(() => {
    const inputElement = inputRef.current;
    if (inputElement) {
      inputElement.addEventListener("keydown", handleKeyDown);
    }

    return () => {
      if (inputElement) {
        inputElement.removeEventListener("keydown", handleKeyDown);
      }
    };
  }, []);

  return (
    <>
      <Card className="h-100 chatSection">
        <Card.Body className="py-0 px-0">
          <Container fluid>
            <Row className="staff-chat w-100">
              <Col
                xl={4}
                lg={12}
                md={12}
                className="p-4 borderRight paddingBottom chat-box-shadow-lt"
              >
                <h3 className="fw-bold mb-3">Messages</h3>
                <SearchInput onChange={handleSearch} />
                {isLoadingAllInboxList ? (
                  <p
                    className="my-5 text-center fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </p>
                ) : isSuccessAllInboxList ? (
                  <div className="staff-inbox">
                    {allInboxList?.map((item) => {
                      let formattedTime = moment(
                        moment.utc(item?.createdDate).toDate()
                      )
                        .local(true)
                        .format("h:mm a");
                      return (
                        <div
                          key={item.inboxId}
                          className={`userChat p-1 mt-1 mobileFlex_reverse d-flex justify-content-between align-items-center ${
                            selectedChat &&
                            selectedChat.inboxId === item.inboxId
                              ? "activeChat"
                              : "hoverChat"
                          }`}
                          onClick={() => handleChatClick(item)}
                        >
                          <div className="d-flex mobileFlex_between align-items-center">
                            <span>
                              <img
                                className="w-25 rounded-circle"
                                src={
                                  item.imageUrl
                                    ? item.imageUrl
                                    : `https://ui-avatars.com/api/?name=${`${item.userName}`}&background=000071&color=fff`
                                }
                                alt="UserImage"
                              />
                            </span>
                            <div className="d-flex flex-column align-items-start ms-3 mt-2">
                              <h5 className="mb-1 fw-semibold mobileTextHeading ">
                                {item.userName ? item.userName : "N/A"}
                              </h5>
                              <p className="w-auto color_light mobileText m-0 message-container">
                                {item.message ? item.message : ""}
                              </p>
                            </div>
                          </div>
                          <div className="d-flex flex-column justify-content-center align-items-end">
                            <p className="color_light fs-6 mb-1">
                              {formattedTime}
                            </p>
                            {item.unreadCount > 0 && (
                              <span className="chatCount text-white rounded">
                                {item.unreadCount}
                              </span>
                            )}
                          </div>
                        </div>
                      );
                    })}
                  </div>
                ) : isErrorAllInboxList ? (
                  <p className="my-5 text-center text-danger fst-italic">
                    Network Error
                  </p>
                ) : null}
              </Col>
              <Col
                xl={8}
                lg={12}
                md={12}
                className="px-0 chatContent chat-box-shadow-rt"
              >
                {selectedChat ? (
                  <>
                    <div className="d-flex justify-content-between mobileFlex_Col py-2 border-bottom px-4 ">
                      <div className="d-flex justify-content-center align-items-center chatActive mb-sm-0 mb-3">
                        <div className="d-flex align-items-center py-0">
                          <span>
                            <img
                              className="rounded-circle"
                              src={
                                selectedChat.imageUrl
                                  ? selectedChat.imageUrl
                                  : `https://ui-avatars.com/api/?name=${`${selectedChat.userName}`}&background=000071&color=fff`
                              }
                              alt="UserImage"
                            />
                          </span>
                          <h5 className="fw-semibold mb-0 ms-3 mobileTextHeading">
                            {selectedChat.userName}
                          </h5>
                        </div>
                      </div>
                      {/* <div className="d-flex justify-content-end align-items-center">
                    <span>
                      <img
                        className="me-4"
                        src={IMAGES.CHAT_CALL_ICON}
                        alt="UserImage"
                      />
                    </span>
                    <span className="">
                      <img
                        className=""
                        src={IMAGES.CHAT_VIDEO_ICON}
                        alt="UserImage"
                      />
                    </span>
                  </div> */}
                    </div>
                    <div>
                      <div
                        className="div-fixed-height"
                        style={{ background: "#FBFBFB" }}
                      >
                        {allMessages !== null
                          ? allMessages?.map((chatData, index) => {
                              let formattedTime = moment(
                                moment.utc(chatData?.createdDate).toDate()
                              )
                                .local(true)
                                .format("h:mm a");
                              return (
                                <>
                                  <div className="d-flex flex-column h-100 px-4 py-3 chatBox">
                                    <div>
                                      {allMessages?.[
                                        index - 1
                                      ]?.createdDate?.split("T")?.[0] !==
                                        chatData?.createdDate?.split(
                                          "T"
                                        )?.[0] && (
                                        <div className="text-center rounded-pill mx-auto chatSpan bg-white">
                                          {moment(chatData?.createdDate).format(
                                            "DD-MM-YYYY"
                                          ) ===
                                          moment(new Date()).format(
                                            "DD-MM-YYYY"
                                          )
                                            ? "Today"
                                            : moment(
                                                chatData?.createdDate
                                              ).format("DD-MM-YYYY")}
                                        </div>
                                      )}

                                      <div>
                                        <Row>
                                          {chatData?.fromUserId !==
                                          user?.userId ? (
                                            <Col lg={8} md={6}>
                                              <div className="chatMessage_left d-flex justify-content-start mobileFlex_Col mb-1">
                                                <img
                                                  className="me-2 mb-2 mb-sm-0 rounded-circle"
                                                  style={{
                                                    width: "50px",
                                                    height: "50px",
                                                  }}
                                                  src={
                                                    chatData?.imageUrl
                                                      ? chatData?.imageUrl
                                                      : `https://ui-avatars.com/api/?name=${chatData?.fromUserName}&background=000071&color=fff`
                                                  }
                                                  alt="UserImage"
                                                />
                                                <div className="w-100">
                                                  {chatData?.file && (
                                                    <div
                                                      className="chat__upload-doc"
                                                      style={{ width: "40%" }}
                                                    >
                                                      <a
                                                        href={chatData?.fileUrl}
                                                        target="_blank"
                                                        rel="noopener noreferrer"
                                                        className="text-decoration-none"
                                                      >
                                                        <div className="d-flex align-items-center p-2 border-bottom">
                                                          {chatData?.file?.endsWith(
                                                            ".pdf"
                                                          ) ? (
                                                            <FaRegFilePdf
                                                              size={22}
                                                              color="#BC0613"
                                                            />
                                                          ) : (
                                                            <BsImage
                                                              size={22}
                                                              color="#000071"
                                                            />
                                                          )}

                                                          <label className="chat__upload_text px-2 fw-bold">
                                                            {chatData?.file}
                                                          </label>
                                                        </div>
                                                        <img
                                                          className={`rounded
                                                        ${
                                                          chatData?.file?.endsWith(
                                                            ".pdf"
                                                          )
                                                            ? "object-fit-contain"
                                                            : "object-fit-cover"
                                                        }`}
                                                          width="100%"
                                                          height="76%"
                                                          src={
                                                            chatData?.file?.endsWith(
                                                              ".pdf"
                                                            )
                                                              ? IMAGES.PDF_DOC
                                                              : chatData?.fileUrl
                                                          }
                                                          alt="doc"
                                                        />
                                                      </a>
                                                    </div>
                                                  )}
                                                  {chatData?.message && (
                                                    <div className="messageSpan">
                                                      {chatData?.message}
                                                    </div>
                                                  )}
                                                  {formattedTime && (
                                                    <div className="chatTime">
                                                      {formattedTime}
                                                    </div>
                                                  )}
                                                </div>
                                              </div>
                                            </Col>
                                          ) : (
                                            <Col
                                              lg={4}
                                              md={6}
                                              className="chatMessage_right text-white d-flex flex-column align-items-end justify-content-end w-100"
                                            >
                                              {chatData?.file && (
                                                <div className="chat__upload-doc">
                                                  <a
                                                    href={chatData?.fileUrl}
                                                    target="_blank"
                                                    rel="noopener noreferrer"
                                                    className="text-decoration-none"
                                                  >
                                                    <div className="d-flex align-items-center p-2 border-bottom">
                                                      {chatData?.file?.endsWith(
                                                        ".pdf"
                                                      ) ? (
                                                        <FaRegFilePdf
                                                          size={22}
                                                          color="#BC0613"
                                                        />
                                                      ) : (
                                                        <BsImage
                                                          size={22}
                                                          color="#000071"
                                                        />
                                                      )}

                                                      <label className="chat__upload_text px-2 fw-bold">
                                                        {chatData?.file}
                                                      </label>
                                                    </div>
                                                    <img
                                                      className={`rounded
                                                        ${
                                                          chatData?.file?.endsWith(
                                                            ".pdf"
                                                          )
                                                            ? "object-fit-contain"
                                                            : "object-fit-cover"
                                                        }`}
                                                      width="100%"
                                                      height="76%"
                                                      src={
                                                        chatData?.file?.endsWith(
                                                          ".pdf"
                                                        )
                                                          ? IMAGES.PDF_DOC
                                                          : chatData?.fileUrl
                                                      }
                                                      alt="doc"
                                                    />
                                                  </a>
                                                </div>
                                              )}
                                              {chatData?.message && (
                                                <span className="mb-1 mobile_margin sender-message">
                                                  {chatData?.message}
                                                </span>
                                              )}
                                              {formattedTime && (
                                                <div className="chatTime align-self-end">
                                                  {formattedTime}
                                                </div>
                                              )}
                                            </Col>
                                          )}
                                        </Row>
                                      </div>
                                    </div>
                                  </div>
                                  <div ref={bottomRef} />
                                </>
                              );
                            })
                          : "No Message"}
                      </div>
                      <div className="border-top">
                        <div className="d-flex mobileFlex_Col justify-content-between align-items-center px-sm-4">
                          <div className="d-flex justify-content-center align-items-center position-relative">
                            <FaRegSmile
                              size={28}
                              color={showPicker ? "#000071" : "#A8A8A8"}
                              className="text-cursor-pointer"
                              onClick={() => setShowPicker((val) => !val)}
                            />
                            {showPicker && (
                              <Picker
                                lazyLoadEmojis={true}
                                onEmojiClick={onEmojiClick}
                                className="position-absolute"
                                style={{ top: "-27.5rem", left: 0 }}
                              />
                            )}
                            <div className="mx-3">
                              <label
                                htmlFor="file-upload"
                                className="text-center"
                              >
                                <FiPaperclip
                                  size={26}
                                  color="#A8A8A8"
                                  className="text-cursor-pointer"
                                />
                              </label>
                            </div>
                            <input
                              size="small"
                              type="file"
                              id="file-upload"
                              name="file-upload"
                              accept="image/png, image/jpeg, application/pdf"
                              onChange={(e) => handleChangeUpload(e)}
                              className="upload-file"
                            />
                          </div>

                          <div className="w-100 d-flex miniFlex_Col">
                            <Form.Group className="position-relative my-3 w-100">
                              <Form.Control
                                as="textarea"
                                rows={1}
                                className="padding rounded-pill"
                                placeholder="Type a message hereâ€¦"
                                value={newMessage}
                                onChange={(e) => setNewMessage(e.target.value)}
                                ref={inputRef}
                                onKeyDown={handleKeyDown}
                                style={{ resize: "none" }}
                              />
                              <Button
                                className="sendIcon rounded-circle"
                                onClick={handleNewMessageSubmit}
                                disabled={
                                  newMessage.trim() === "" &&
                                  !uploadImageURL?.keyName
                                }
                              >
                                <img src={IMAGES.SEND_ICON} alt="messageIcon" />
                              </Button>
                            </Form.Group>
                            {/* <Button className="bg-transparent">
                              <img
                                className=""
                                type="button"
                                src={IMAGES.MIC_ICON}
                                alt="mic"
                                onClick={handleNewMessageSubmit}
                              />
                            </Button> */}
                          </div>
                        </div>
                        {selectedFiles?.name && (
                          <div
                            className="d-flex align-items-center pb-2"
                            style={{ marginLeft: "7.5rem" }}
                          >
                            <GrDocumentCloud
                              size={25}
                              style={{ color: "#000071" }}
                            />
                            <h6 className="file-name mb-0 ms-2">
                              {selectedFiles?.name}
                            </h6>
                            <span className="mx-3 text-cursor-pointer">
                              <AiOutlineClose
                                size={18}
                                onClick={handleRemoveFile}
                              />
                            </span>
                          </div>
                        )}
                      </div>
                    </div>
                  </>
                ) : (
                  <h5 className="mb-0 text-center text-black-50 d-flex justify-content-center align-items-center h-100">
                    Welcome to our chat system <br /> Please feel free to start
                    typing your message
                  </h5>
                )}
              </Col>
            </Row>
          </Container>
        </Card.Body>
      </Card>
    </>
  );
};

export default Chat;
